.circle-container {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow-x: hidden;
    top: 0px;

}
 

.animated-circle {
    position: absolute;
    width: 300px;
    height: 300px;
    box-shadow: 2px 10px 100px 10px rgb(161, 255, 10);
    border-radius: 50%;
    animation: floatRandom 40s infinite ease-in-out;
    opacity: 1;

 }

/* Keyframes for smooth, random movement */
@keyframes floatRandom {
    0% {
        top: 10%;
        left: 10%;
        transform: scale(1);
    }

    25% {
        top: 30%;
        left: 70%;
        transform: scale(1.05);
    }

    50% {
        top: 60%;
        left: 20%;
        transform: scale(1.1);
    }

    75% {
        top: 40%;
        left: 60%;
        transform: scale(1.03);
    }

    100% {
        top: 10%;
        left: 10%;
        transform: scale(1);
    }
}


@media(max-width:900px) {
    .animated-circle {
        position: absolute;
        width: 150px;
        height: 150px;
        box-shadow: 2px 10px 100px 10px rgb(161, 255, 10);
        border-radius: 50%;
        animation: floatRandom 40s infinite ease-in-out;
        opacity: 1;

     }
}