 .animatedCirc {
     position: absolute;
     height: 100vh;
     width: 100%;
     display: flex;
     justify-content: center;
     align-items: center;
 
 }

 .animatedCirc-wrapper {
     position: relative;
     width: 100%;
     height: 100vh;


 }


 .experience-header {
     display: flex;
     align-items: center;
     justify-content: flex-start;
     position: relative;
     height: auto;
     width: 100%;
     z-index: 2;


 }

 .experience-header h1 {
     color: #292929;
     font-size: 50px;
     letter-spacing: 10px;
     right: 0px;
     width: 100%;
     height: auto;
     font-weight: lighter;
 }

 .experience {
     width: 100%;
     height: auto;
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     margin: 50px auto;
     background-color: #1b1b1b;
     position: relative;

 }

 .exp-background {
     position: absolute;
     top: -50%;
     display: flex;
     margin: auto;
     height: 100%;
     width: 100%;
     background-color: #1b1b1b;
     z-index: -1;
     /* z-index: -99999; */
 }

 .exp-contentWrapper {
     width: 100%;
     height: auto;
     display: flex;
     justify-content: center;
     align-items: center;
     margin: auto;
     flex-direction: column;
     padding: 5px;
     /* background-color: rebeccapurple; */

 }



 @media (max-width: 800px) {
     .exp-contentWrapper {
         width: 100%;
         height: auto;
         display: flex;
         justify-content: center;
         align-items: center;
         margin: auto;
         flex-direction: column;
         padding: unset;
     }

     .experience-header {
         display: flex;
         align-items: center;
         justify-content: flex-start;
         position: relative;
         height: auto;
         width: 100%;
         z-index: 2;


     }

     .experience-header h1 {
         color: #292929;
         font-size: 30px;
         letter-spacing: 10px;
         right: 0px;
         width: 100%;
         height: auto;
         font-weight: lighter;

         padding-left: 20px;
     }
 }