.about {
    justify-content: center;
    align-items: center;
    display: flex;
    width: 100%;
    gap: 30px;
    flex-direction: column;
    z-index: 1;
    /* margin-top: 100px; */
    padding-bottom: 50px;
    border-radius: 0px 0px 70px 70px;
    /* background-color: #292929;
    background-color: #1b1b1b; */

}


.about-section1 {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
    position: relative;
    padding: 30px;
    height: auto;
    width: 70%;
    gap: 60px;

}

.about-writeUpWrapper {
    width: 100%;
    height: auto;
    display: flex;
    gap: 5px;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    overflow: hidden;
    padding: 10px;

    gap: 20PX;
}

.about-writeUpWrapper .about-bioText {
    color: #898989;
    width: 90%;
    height: 100%;
    font-size: 18px;
    cursor: pointer;
    justify-content: center;
    align-items: center;
    margin: auto;
    padding: 15px 18px;

    display: flex;
    border-radius: 5px;
}

.about-writeUpWrapper .about-bioText:hover {
    background-color: #292929;
    transition: all 50ms ease;
    box-shadow: 0px 0px 5px rgb(161, 255, 10);
    color: white;
    border-radius: 20px;
    transform: scale(1.1, 1.1);
}

.about-imageWrapper {
    display: flex;
    height: auto;
    width: auto;
    z-index: 10;
}

.about-section2 {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    margin: auto;
    gap: 50px;
    position: relative;
    justify-content: center;
    align-items: center;
}

.about-imageContainer {
    width: 350px;
    height: 500px;
    object-fit: contain;
}

@media (max-width: 768px) {
    .about {
        justify-content: center;
        align-items: center;
        display: flex;
        width: 100%;
        gap: 10px;
        z-index: 1;
        flex-direction: column;
        margin: unset;

        border-radius: 0px 0px 100px 100px;

    }

    .about-section1 {
        width: 100%;

        display: flex;

        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: auto;
        gap: 10px;
    }


    .about-writeUpWrapper {
        display: flex;
        gap: 15px;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border-left: 0px;
        width: 100%;
        height: auto;
    }

    .about-writeUpWrapper .about-bioText {
        color: #898989;
        width: 100%;

        height: auto;
        font-size: 16px;
        font-style: italic;
        padding: 10px 20px;
        border-radius: 10px;
    }

    .about-writeUpWrapper .about-bioText:hover {
        border-radius: 10px;

    }

    .about-section2 {
        display: flex;
        gap: 30px;
        width: 100%;

    }

    .about-imageWrapper {
        display: flex;
        height: auto;
        width: 100%;
        justify-content: center;
        align-items: center;
        margin: unset;

    }
}