 .slider__container {
   max-width: 100%;
   height: auto;
   margin: auto;

 }

 .slick-track {
   display: flex;
   align-items: flex-start;
   justify-content: center;
   padding: 20px 0px;
 }

 .slick-slide>div {
   position: relative;
   margin: 0px 10px;
   display: flex;
   justify-content: space-between;
   align-items: center;
   position: relative;
   height: auto;
 }

 .slider__item {
   display: flex;
   height: auto;
   width: 100%;
   position: relative;
 }


 .custom-slider-btn-wrapper {
   position: absolute;
   bottom: -30px;
   left: 50%;
   transform: translateX(-50%);
   display: flex;
 }


 .custom-arrow:hover {
   background-color: rgba(255, 255, 255, 0.2)
 }

 .custom-btn-container {
   position: relative;
   justify-content: center;
   align-items: center;
   height: auto;
 }

 .custom-slider-btns {
   position: relative;
   display: flex;
   gap: 20px;
   left: 0px;
   right: 0px;
   width: 30%;
   height: auto;
   margin: auto;
   margin-top: 10px;
   justify-content: space-between;
   align-items: center;
   align-self: center;
 }

 .custom-btn {
   border: none;
   font-size: 2.2rem;
   display: flex;
   cursor: pointer;
   background: transparent;

   background-color: transparent;
   border-radius: 20px;
   overflow: hidden;
 }


 .custom-arrow-btn {
   background-color: #29292929;
   width: 100%;
   height: 100%;
   padding: 10px 20px;
   transition: all ease 350ms;



 }

 .custom-arrow-btn:hover {
   background-color: transparent;
   scale: 2;
 }

 @media (max-width:900px) {

   .slider__container {
     max-width: 100%;
     height: 100%;
     margin: auto;
   }
 }