@import url('https://fonts.googleapis.com/css2?family=Pacifico&family=Playwrite+DK+Loopet:wght@100..400&display=swap');

* {
  padding: 0px;
  margin: 0px;
  color: #fff;
  box-sizing: border-box;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  scroll-behavior: smooth;
}

body {
  background-color: #1b1b1b;
  /* cursor: none; */
}

.App {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
}

.section {
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: inherit;
  transition: background-color 0.5s ease;
}

.mailShortcutBtn {
  display: flex;
  position: fixed;
  z-index: 999;
  right: 40px;
  bottom: 40px;
  font-size: 25px;
  background-color: #181818;
  padding: 15px;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  cursor: pointer;
  z-index: 999999;
  transition: all ease-in-out 350ms;
}

.mailShortcutBtn:hover {
  scale: 1.2;
}