.exp-wrapper {
    width: 100%;
    height: auto;
    display: flex;
    position: relative;
    background-color: #292929e8;
    backdrop-filter: blur(5px);
    justify-content: center;
    align-items: flex-start;
    border-radius: 20px;
    padding: 20px 30px;
    gap: 5px;
    cursor: pointer;
    z-index: 3;
    transition: 350ms all ease-in-out; 
}

.exp-wrapper:hover {
    scale: 1.1, 1;
}

.exp-content {
    flex: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    gap: 10px;
}

.exp-titleWrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}

.exp-title {
    flex: 1;
    display: flex;
    font-size: 25px;
    color: #898989;
}

.exp-company {
    color: #898989;
    height: 100%;
    display: flex;
    flex: 1;
    font-size: 20px;
    font-weight: bold;
    font-style: italic;
    align-items: flex-start;
    justify-content: flex-end
}

.exp-child-wrapper {
    margin-bottom: 30px;
    display: flex;
    justify-content: space-between;
    gap: 10px;
    width: 100%;
    flex-direction: row;
}

.exp-location {
    font-size: 12px;
    font-style: italic;
    color: #898989;
}

.exp-duration {
    font-size: 12px;
    font-style: italic;
    color: #898989;
}

.exp-accomplishments-section {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
}

.exp-accomplishments-wrapper {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    height: auto;
    margin: auto;
    gap: 5px;
    border-bottom: 1px solid #898989;


    padding: 10px 0px;
}

.exp-accomplishments-wrapper:last-child {
    border-bottom: unset;
}

.exp-accomplishments {
    font-size: 14px;
    width: 100%;


}

@media (max-width:900px) {
    .exp-wrapper {
        padding: 10px 15px;
        height: 450px;
    }

    .exp-content {
        height: 100%;
        gap: unset;

    }

    .exp-content {
        flex: 1;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        gap: unset;
    }
}